.burger_container {
  width: 34px;
  height: 26px;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  position: relative;
  display: none;
  cursor: pointer;
}
.burger_line {
  width: 100%;
  height: 4px;
  border-radius: 3px;
  background-color: rgba(39, 28, 77, 0.6);
}

@media (max-width: 1350px) {
  .burger_container {
    display: flex;
  }
}
